<script>
import axios from 'axios';
import Swal from 'sweetalert2';
import NewsForm from '@/components/News.vue';
const SERVER_URL = process.env.VUE_APP_SERVER_URL;

export default {
  name: 'Home',
  components: {
    NewsForm,
  },
  data() {
    return {
      user: {
        id: '',
        password: '',
        name: '',
        email: '',
        phone: '',
        address: '',
      },
    };
  },
  methods: {
    register() {
      axios
        .post(`${SERVER_URL}/user/register`, this.user)
        .then(({ data }) => {
          console.log(data);
          if (data === 'success') {
            this.$router.replace('/login');
            //  .
            this.$store.state.userName = this.user.name;
          }
          Swal.fire({
            position: 'top',
            background: '#F6F6F6',
            icon: 'success',
            iconColor: '#F2CB61',
            text: 'Pent House   .',
            showConfirmButton: false,
            timer: 2000,
          });
        })
        .catch(() => {
          Swal.fire({
            icon: 'warning',
            text: '  ID  .',
            showClass: {
              popup: 'animate__animated animate__fadeInDown',
            },
            hideClass: {
              popup: 'animate__animated animate__fadeOutUp',
            },
            background: '#F6F6F6',
            confirmButtonText: '',
            confirmButtonColor: '#FFBB00',
          });
        });
    },
  },
};
</script>
