<template>
  <div>
    <!-- ======= Header ======= -->
    <header id="header" class="fixed-top">
      <div class="container d-flex align-items-center">
        <h1 class="logo mr-auto">
          <a href="/">PENT House</a>
        </h1>
        <!-- Uncomment below if you prefer to use an image logo -->
        <!-- <a href="index.html" class="logo mr-auto"><img src="assets/img/logo.png" alt="" class="img-fluid"></a>-->

        <nav class="nav-menu d-none d-lg-block" v-if="getAccessToken">
          <ul>
            <li class="active"><a href="/">HOME</a></li>
            <li><a href="#about">ABOUT</a></li>
            <li><a href="#specials">NOTICE</a></li>
            <li><a href="#events">NEWS</a></li>
            <li><a href="#contact">CONTACT</a></li>

            <!--    -->

            <li class="book-a-table text-center">
              <router-link to="/me">{{ getUserName }} </router-link>
            </li>
            <li class="book-a-table text-center">
              <a href="#" id="loginResult2" @click.prevent="onClickLogout"
                ></a
              >
            </li>
          </ul>
        </nav>

        <nav class="nav-menu d-none d-lg-block" v-else>
          <!--    -->
          <ul>
            <li class="active"><a href="/">HOME</a></li>
            <li><a href="#about">ABOUT</a></li>
            <li><a href="#specials">NOTICE</a></li>
            <li><a href="#events">NEWS</a></li>
            <li><a href="#contact">CONTACT</a></li>

            <li class="book-a-table text-center">
              <a href="/#book-a-table" id="loginResult2"></a>
            </li>
            <li class="book-a-table text-center">
              <router-link to="/login"></router-link>
            </li>
          </ul>
        </nav>
        <!-- .nav-menu -->
      </div>
    </header>
    <!-- End Header -->
  </div>
</template>

<script>
import Vue from 'vue';
import VueRouter from 'vue-router';
import axios from 'axios';
// import $ from 'jquery';
import Swal from 'sweetalert2';

Vue.use(VueRouter);
const SERVER_URL = process.env.VUE_APP_SERVER_URL;
//mapGetters 
import { mapGetters } from 'vuex';

export default {
  // name: 'Login',
  data() {
    return {
      user: {
        userid: '',
        userpwd: '',
      },
    };
  },
  created() {
    console.log(
      '  ' + sessionStorage.getItem('loginInfo')
    );
    if (sessionStorage.getItem('loginInfo') != null) {
      console.log(' /.' + sessionStorage.getItem('loginInfo'));
      //  .
      axios.defaults.headers.common['auth-token'] = JSON.parse(
        sessionStorage.getItem('loginInfo')
      )['auth-token'];
      axios
        .get(`${SERVER_URL}/user/info`)
        .then(() => {
          console.log('then ');
          // .
          this.$store.commit(
            'LOGIN',
            JSON.parse(sessionStorage.getItem('loginInfo'))
          ); //user.
        })
        .catch(() => {
          console.log('  ');
          sessionStorage.removeItem('loginInfo');
          this.$store.dispatch('LOGOUT').then(() => this.$router.replace('/'));
          console.log(
            '  ' + sessionStorage.getItem('loginInfo')
          );
        });
    }
  },
  computed: {
    ...mapGetters(['getAccessToken', 'getUserId', 'getUserName']),
  },

  methods: {
    onClickLogout() {
      this.$store
        .dispatch('LOGOUT')
        .then(() => this.$router.replace('/').catch(() => {}));
      Swal.fire({
        position: 'top',
        background: '#F6F6F6',
        icon: 'success',
        iconColor: '#F2CB61',
        text: '',
        showConfirmButton: false,
        timer: 2000,
      });
    },
  },
};
</script>
