<template>
  <div>
    <!-- =======    ======= -->
    <section id="book-a-table" class="book-a-table">
      <div class="container" data-aos="fade-up" style="margin-top: 150px;">
        <div class="section-title">
          <h2>Membership</h2>
          <p>   </p>
        </div>

        <form
          action="forms/book-a-table.php"
          id="mform"
          method="post"
          role="form"
          class="php-email-form"
          data-aos="fade-up"
          data-aos-delay="100"
        >
          <div class="form-row">
            <div class="col-lg-4 col-md-6 form-group">
              <input
                type="text"
                name="name"
                class="form-control"
                readonly="readonly"
                :value="user.name"
              />
              <div class="validate"></div>
            </div>
            <div class="col-lg-4 col-md-6 form-group">
              <input
                type="email"
                class="form-control"
                readonly="readonly"
                :value="user.email"
              />
              <div class="validate"></div>
            </div>
            <div class="col-lg-4 col-md-6 form-group">
              <input
                type="text"
                class="form-control"
                readonly="readonly"
                :value="user.phone"
              />
              <div class="validate"></div>
            </div>
            <div class="col-lg-6 col-md-6 form-group">
              <input
                type="text"
                class="form-control"
                readonly="readonly"
                :value="user.id"
                style="color: #F2CB61"
              />
              <div class="validate"></div>
            </div>
            <div class="col-lg-6 col-md-6 form-group">
              <input
                type="password"
                class="form-control"
                readonly="readonly"
                :value="user.password"
                style="color: #F2CB61"
              />
              <div class="validate"></div>
            </div>
          </div>
          <div class="form-group">
            <textarea
              class="form-control"
              name="message"
              rows="2"
              readonly="readonly"
              v-model="user.address"
            ></textarea>
            <div class="validate"></div>
          </div>
          <div class="text-center">
            <button type="button" style="margin-right: 20px;" @click="withdraw">
                 
            </button>
            <button type="button" style="margin-right: 20px;" @click="modify">
                 
            </button>
          </div>
        </form>
      </div>
    </section>
  </div>
</template>
<!-- <h2></h2>
     : {{ user.username }}<br />
     : {{ user.email }}<br />
     : {{user.phone}}
     : {{ user.userid }}<br />
     : {{user.password}}
     : {{ user.address }}<br /> 
  </div> -->
<script>
import axios from 'axios';
import Swal from 'sweetalert2';

const SERVER_URL = process.env.VUE_APP_SERVER_URL;

export default {
  data() {
    return {
      user: '',
    };
  },
  created() {
    //  .
    axios
      .get(`${SERVER_URL}/user/info`)
      .then((response) => {
        // console.log(response.data.user);
        this.user = response.data.user;
        console.log(this.user);
      })
      .catch(() => {
        this.$store.dispatch('LOGOUT').then(() => this.$router.replace('/'));
      });
  },
  methods: {
    withdraw() {
      Swal.fire({
        title: ' ',
        text: 'PentHouse    . ?',
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#FFBB00',
        cancelButtonColor: '#5D5D5D',
        confirmButtonText: ', .',
        cancelButtonText: '',
      }).then((result) => {
        if (result.isConfirmed) {
          axios
            .delete(`${SERVER_URL}/user/${this.user.id}`)
            .then(({ data }) => {
              this.$store.dispatch('LOGOUT'); // 
              if (data === 'success') {
                Swal.fire({
                  title: ' ',
                  text: '  .',
                  icon: 'success',
                  confirmButtonText: '',
                  confirmButtonColor: '#5D5D5D',
                });
              }
              this.$router.replace('/');
            })
            .catch(() => {
              alert('  .');
            });
        }
      });
    },
    modify() {
      this.$router.push('/memberupdate');
    },
  },
};
</script>
